/* eslint-disable lit-a11y/click-events-have-key-events */
import '@vonage/vwc-button';
import '@vonage/vwc-checkbox';
import '@vonage/vwc-formfield';
import '@vonage/vwc-icon-button';
import '@vonage/vwc-list/vwc-list-item.js';
import '@vonage/vwc-radio';
import '@vonage/vwc-select';
import '@vonage/vwc-textfield';
import { css, html, LitElement } from 'lit';

export class VveBreakout extends LitElement {
  static get styles() {
    return css`
      :host {
        display: block;
        color: var(--vve-breakout-text-color, #000);
      }

      .content {
        display: none;
        border-left: 1px solid black;
        border-right: 1px solid black;
        padding: 10px 0;
        width: 100%;
      }

      .collapsible {
        display: flex;
        width: 100%;
        border: 1px solid black;
        align-items: center;
        padding: 16px 0;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }

      .add-person-options {
        display: none;
        z-index: 10;
        position: absolute;
        background-color: #fff;
        width: 100%;
        border-radius: 8px;
        box-shadow: 0px 0px 10px #888;
        -webkit-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.4);
        -moz-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.4);
      }

      .change-room {
        width: 100px;
      }

      .room-time {
        display: flex;
        justify-content: space-between;
        width: 100%;
        border: 1px solid black;
        align-items: center;
        padding: 16px 0;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }

      .add-time-button {
        padding-left: 16px;
        --mdc-shape-small: 16px;
      }

      .container {
        display: flex;
        flex-direction: column;
        height: calc(100vh - 150px);
        position: relative;
      }

      .create-or-close-button {
        position: absolute;
        bottom: 16px;
        left: 0;
        right: 0;
      }

      .card {
        margin-bottom: 16px;
        padding: 0 16px;
      }

      .breakout-menu {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 16px;
      }

      #add-room {
        color: white;
        background-color: #6200ee;
        border-radius: 50%;
      }

      .participants {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 8px;
      }

      .participants > div {
        display: flex;
        flex-direction: row;
        align-items: center;
      }

      .room-participant {
        padding: 8px;
      }

      .participant-buttons {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        padding: 8px;
      }

      .participant-buttons > * {
        margin-left: 10px;
      }
      vwc-textfield {
        padding: 8px;
        width: calc(100% - 16px);
      }
      hr {
        width: 90%;
      }
    `;
  }

  static get properties() {
    return {
      role: { type: String },
      room: { type: Object },
      parent: { type: Object },
      roomView: { type: Boolean },
      rooms: { type: Array },
      roomCount: { type: Number },
      goBack : {type: Function },
      timer: { type: Number },
      createRoom: { type: Function },
      joinRoom: { type: Function },
      randomDistribution: { type: Boolean },
      participants: { type: Array },
    };
  }

  connectedCallback() {
    super.connectedCallback();
  }

  constructor() {
    super();
    this.room = {};
    this.roomView = false;
    this.rooms = [];
    this.roomCount = 1;
    this.timer = 5;
    this.randomDistribution = true;
    this.participants = [];
  }

  async updated(changedProperties) {
    if (changedProperties.get('room')) {
      this.participants = Object.values(this.room.participants).map(
        participant => {
          const _participant = participant;
          _participant.roomId = this.room.roomId;
          return _participant;
        }
      );

      this.room.on('participantJoined', participant => {
        const _participant = participant;
        _participant.roomId = this.room.roomId;
        this.participants = [...this.participants, _participant];
      });

      this.room.on('participantLeft', participantLeft => {
        this.participants = this.participants.filter(
          participant => participant.id !== participantLeft.id
        );
      });
    }
  }

  createRooms() {
    if (!this.roomView) {
      return html`<h2>Create breakout</h2>
        <p>Create Breakout Sessions</p>
        <p>
          You can easily create breakout sessions if you're the host or cohost.
          You can create the sessions any time during the meeting and start them
          when you're ready.
        </p>

        <vwc-select
          id="room"
          label="No. of rooms"
          style="margin-right: 10px"
          @change=${this.onRoomChanged}
        >
          <vwc-list-item></vwc-list-item>
          <vwc-list-item value="1">1</vwc-list-item>
          <vwc-list-item value="2">2</vwc-list-item>
          <vwc-list-item value="3">3</vwc-list-item>
          <vwc-list-item value="4">4</vwc-list-item>
        </vwc-select>

        <vwc-select id="timer" label="timer" @change=${this.onTimeChanged}>
          <vwc-list-item></vwc-list-item>
          <vwc-list-item selected value="0">5 Min</vwc-list-item>
          <vwc-list-item value="1">10 Min</vwc-list-item>
          <vwc-list-item value="2">15 Min</vwc-list-item>
          <vwc-list-item value="3">20 Min</vwc-list-item>
        </vwc-select>
        <div>
          <p>Divide Students</p>

          <vwc-formfield label="Manually">
            <vwc-radio
              checked
              name="distribution"
              id="manually"
              @checked=${this._onRadioToggle}
            ></vwc-radio>
          </vwc-formfield>
          <vwc-formfield label="Randomly">
            <vwc-radio
              name="distribution"
              id="randomly"
              @checked=${this._onRadioToggle}
            ></vwc-radio>
          </vwc-formfield>
        </div>`;
    }
    return html``;
  }

  _onRadioToggle(e) {
    if (e.target.id === 'manually') {
      this.randomDistribution = false;
    } else {
      this.randomDistribution = true;
    }
  }

  joinRooms() {
    if (this.roomView) {
      return html`
        <h2>Join Breakout</h2>
        <div class="breakout-menu">
          <span>Breakouts (${this.rooms.length})</span>
          <div style="display: flex; flex-direction: row;">
            <div style="position: relative;margin-right:10px">
              <vwc-button
                id="add-person"
                icon="add-user-solid"
                @click=${this._showHideAddPersonOptions}
              ></vwc-button>
            </div>
            <vwc-button
              label="Add Room"
              layout="filled"
              id="add-room"
              icon="app-plus-line"
              @click=${this._addAdditionalRoom}
            ></vwc-button>
          </div>
        </div>
        <div class="add-person-options">
          <vwc-textfield
            iconTrailing="search"
            label="search"
            outlined
          ></vwc-textfield>
          ${this.participants.map(
            participant => html`
              <div class="participants" id=${participant.id}>
                <div>
                  <vwc-checkbox id=${participant.roomId}></vwc-checkbox>
                  <span class="participant-name">${participant.name}</span>
                </div>
                <vwc-select outlined class="change-room" id="">
                  <vwc-list-item></vwc-list-item>
                  ${this.rooms.map((room, i) => {
                    if (room.roomId !== participant.roomId) {
                      return html`
                        <vwc-list-item value="${room.roomId}"
                          >${i + 1}</vwc-list-item
                        >
                      `;
                    }
                    return html`
                      <vwc-list-item selected value="${room.roomId}"
                        >${i + 1}</vwc-list-item
                      >
                    `;
                  })}
                </vwc-select>
              </div>
              <hr />
            `
          )}

          <div class="participant-buttons">
            <vwc-button
              id="cancel-change"
              @click=${this._onParticipantEditCancel}
              layout="outlined"
              label="Cancel"
            >
              <button type="submit" style="display: none;"></button
            ></vwc-button>
            <vwc-button
              id="save-participants"
              @click=${this._onParticipantEdit}
              label="Assign to Breakout Room"
              layout="filled"
            >
              <button type="submit" style="display: none;"></button
            ></vwc-button>
          </div>
        </div>
        <div
          style="max-height: calc(100vh - 360px); overflow-y: scroll; overflow-x: hidden;"
        >
          ${this.rooms.map(
            (room, i) => html`
              <div class="card">
                <div class="collapsible" @click=${this.roomExpand}>
                  <vwc-icon-button
                    class="collapse-icon"
                    icon="expand_more"
                  ></vwc-icon-button>
                  Breakout ${i + 1} (${Object.keys(room.participants).length})
                </div>
                <div class="content">
                  ${Object.keys(room.participants).map(
                    id => html`
                      <div class="room-participant">
                        ${room.participants[id].name}
                      </div>
                    `
                  )}
                </div>
                <div class="room-time">
                  <vwc-button
                    disabled
                    class="add-time-button"
                    outlined
                    icon="add"
                    >1</vwc-button
                  >
                  <div class="time-left">00:00:00</div>
                  <div>
                    <vwc-icon-button
                      disabled
                      icon="play_arrow"
                    ></vwc-icon-button>
                    <vwc-icon-button disabled icon="pause"></vwc-icon-button>
                  </div>
                </div>
              </div>
            `
          )}
        </div>
      `;
    }
    return html``;
  }

  _onParticipantEdit() {
    const elems = this.shadowRoot.querySelectorAll('.participants');
    for (let i = 0; i < elems.length; i += 1) {
      const checkBox = elems[i].querySelector('vwc-checkbox');
      const pName = elems[i].querySelector('.participant-name').textContent;
      if (checkBox.checked) {
        const roomIdToSend = elems[i].querySelector('vwc-select').value;
        if (roomIdToSend !== checkBox.id) {
          // send participant to new room
          this.joinRoom(elems[i].id, roomIdToSend, pName);
        }
      }
    }
  }

  _onGoBack() {
    this.goBack();
  }

  _onParticipantEditCancel() {
    this._showHideAddPersonOptions();
  }

  _showHideAddPersonOptions() {
    const view = this.shadowRoot.querySelector('.add-person-options');
    if (view.style.display === 'block') {
      view.style.display = 'none';
    } else {
      view.style.display = 'block';
    }
  }

  // eslint-disable-next-line class-methods-use-this
  roomExpand(e) {
    let { target } = e;
    if (e.target !== e.currentTarget) {
      target = e.target.parentElement;
    }

    const content = target.nextElementSibling;

    const icon = target.querySelector('.collapse-icon');

    if (content.style.display === 'block') {
      icon.icon = 'expand_more';
      content.style.display = 'none';
    } else {
      icon.icon = 'expand_less';
      content.style.display = 'block';
    }
  }

  onRoomChanged(e) {
    this.roomCount = e.target.value;
  }

  onTimeChanged(e) {
    this.timer = e.target.value;
  }

  _addAdditionalRoom() {
    this.createRoom().then(room => {
      room.on('participantJoined', participant => {
        const _participant = participant;
        _participant.roomId = this.room.roomId;
        this.participants = [...this.participants, _participant];
      });

      room.on('participantLeft', participantLeft => {
        this.participants = this.participants.filter(
          participant => participant.id !== participantLeft.id
        );
      });
      this.rooms = [...this.rooms, room];
    });
  }

  _createOrCloseRoom() {
    if (!this.roomView) {
      for (let i = 0; i < this.roomCount; i += 1) {
        this.createRoom().then(room => {
          room.on('participantJoined', participant => {
            const _participant = participant;
            _participant.roomId = this.room.roomId;
            this.participants = [...this.participants, _participant];
          });

          room.on('participantLeft', participantLeft => {
            this.participants = this.participants.filter(
              participant => participant.id !== participantLeft.id
            );
          });
          this.rooms = [...this.rooms, room];
        });
      }
    } else {
      // We dont need to send message for each room.
      // We can just send one message to close all rooms.
      this.parent.signal({
        data: this.room.roomId,
        type: 'close-all',
      });
      this.rooms = [];
      this.roomCount = 1;
      this.timer = 5;
    }
    this.roomView = !this.roomView;
  }

  _listAllParticipants() {
    const participants = [];
    // participants in breakout rooms
    this.rooms.forEach(room => {
      Object.values(room.participants).forEach(participant => {
        participants.push(participant);
      });
    });

    for (let i = 0; i < this.rooms.length; i += 1) {
      for (
        let j = 0;
        j < Object.values(this.rooms[i].participants).length;
        j += 1
      ) {
        const participant = Object.values(this.rooms[i].participants)[j];
        participant.roomId = this.rooms[i].roomId;
        participants.push(participant);
      }
    }
    // participants in the main room
    for (let j = 0; j < Object.values(this.room.participants).length; j += 1) {
      const participant = Object.values(this.room.participants)[j];
      participant.roomId = this.room.roomId;
      participants.push(participant);
    }
    return participants;
  }

  _renderTeacher() {
    return html` <div>
        <vwc-button
          appearance="ghost"
          style="width:7px;"
          @click=${this._onGoBack}
          icon="arrow-left-line"
          aria-label="Go Back"
        ></vwc-button>

        ${this.createRooms()} ${this.joinRooms()}
      </div>
      <vwc-button
        class="create-or-close-button"
        layout="outlined"
        shape="pill"
        label="${this.roomView ? 'Close All Rooms' : 'Create Rooms'}"
        icon="${this.roomView ? 'logout' : 'group_add'}"
        @click=${this._createOrCloseRoom}
      ></vwc-button>`;
  }

  _renderStudent() {
    this.role = 'participant';
    return html``;
  }

  render() {
    return html`
      <div class="container">
        ${this.role !== 'host' ? this._renderTeacher() : this._renderStudent()}
      </div>
    `;
  }
}
