
import "@vonage/vivid/badge";
import "@vonage/vivid/button";
import { css, html, LitElement } from 'lit';
  
export class VveWaiting extends LitElement {
  static  get styles() { 
    return [
    css`

      :host {
        display: block;
        color: var(--vve-waiting-text-color, #000);
      }

      .content {
        display: none;
        border-left: 1px solid black;
        border-right: 1px solid black;
        padding: 10px 0;
        width: 100%;
      }

      .collapsible {
        display: flex;
        width: 100%;
        border: 1px solid black;
        align-items: center;
        padding: 16px 0;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }

      .add-person-options {
        display: none;
        z-index: 10;
        position: absolute;
        background-color: #fff;
        width: 100%;
        border-radius: 8px;
        box-shadow: 0px 0px 10px #888;
        -webkit-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.4);
        -moz-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.4);
      }

      .change-room {
        width: 100px;
      }

      .room-time {
        display: flex;
        justify-content: space-between;
        width: 100%;
        border: 1px solid black;
        align-items: center;
        padding: 16px 0;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }

      .add-time-button {
        padding-left: 16px;
        --mdc-shape-small: 16px;
      }

      .container {
        display: flex;
        flex-direction: column;
        height: calc(100vh - 150px);
        position: relative;
      }

      .create-or-close-button {
        position: absolute;
        bottom: 16px;
        left: 0;
        right: 0;
      }

      .card {
        margin-bottom: 16px;
        padding: 0 16px;
      }

      .waiting-header {
        font-family: 'Spezia';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
      }

      #add-room {
        color: white;
        background-color: #6200ee;
        border-radius: 50%;
      }

      .participants {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 8px;
      }

      .spacer {
        flex-grow: 1;
      }

      .participants > div {
        display: flex;
        flex-direction: row;
        align-items: center;
      }

      .room-participant {
        padding: 8px;
      }

      .participant-buttons {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        padding: 8px;
      }

      .participant-buttons > * {
        margin-left: 10px;
      }
      vwc-textfield {
        padding: 8px;
        width: calc(100% - 16px);
      }
      hr {
        width: 90%;
      }
    `]
  };
  

  static get properties() {
    return {
      role: { type: String },
      room: { type: Object },
      parent: { type: Object },
      participants: { type: Array },
      goBack : {type: Function },
      allowParticipant: { type: Function },
      dismissParticipant: { type: Function },
    };
  }

  connectedCallback() {
    super.connectedCallback();
  }

  constructor() {
    super();
    this.room = {};
    this.hostView = true;
    this.rooms = [];
    this.roomCount = 1;
    this.timer = 5;
    this.participants = [];

  }

  renderHostView() {
    return html`
      <p class="waiting-header">
      <vwc-button 
      appearance="ghost"
      @click=${this._onGoBack}
       icon='arrow-left-line'
        aria-label="Go Back"></vwc-button>
        Waiting to Get in &nbsp;<vwc-badge text='${this.participants?.length}' connotation='cta'  appearance='subtle' density='condensed' shape='pill'></vwc-badge> 
      </p>

      ${this.participants.map(
        participant => html`
          <div class="participants" id=${participant.id}>
            <div>
              <span class="participant-name">${participant.name}</span>
            </div>
            <div class="spacer"></div>
            <vwc-button
              id="save-participants"
              @click=${this._onParticipantDismiss}
              label="Dismiss"
              appearance='ghost'
            >
              <button type="submit" style="display: none;"></button
            ></vwc-button>

            <vwc-button
              id="save-participants"
              data-participant-name=${participant.name}
              @click=${this._onParticipantAllow}
              label="Admit"
              connotation="cta"
              appearance='ghost'
            >
              <button type="submit" style="display: none;"></button
            ></vwc-button>
          </div>
          <hr />
        `
      )}
    `;
  }

  _onGoBack() {
    this.goBack();
  }

  _onParticipantAllow(e) {
    this.allowParticipant(e.target.dataset.participantName).then(status =>
      console.log(status)
    );
  }

  _onParticipantDismiss(e) {
    this.dismissParticipant(e.target.dataset.participantName).then(status =>
      console.log(status)
    );
  }

  _renderHost() {
    return html` <div>${this.renderHostView()}</div> `;
  }

  _renderParticipant() {
    this.role = 'participant';
    return html``;
  }

  render() {
    return html`
      <div class="container">
        ${this.role !== 'host' ? this._renderHost() : this._renderParticipant()}
      </div>
    `;
  }
}
