import '@material/mwc-button';
import '@material/mwc-textfield';
import { css, html, LitElement } from 'lit';
import { unsafeSVG } from 'lit/directives/unsafe-svg.js';

const close =
  '<path d="M0 0h24v24H0z" fill="none"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>';
const play =
  '<path d="M0 0h24v24H0V0z" fill="none"/><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 14.5v-9l6 4.5-6 4.5z"/>';

export class VveTextChat extends LitElement {
  static get styles() {
    return css`
      :host {
        display: inline-block;
        padding: 5px;
        color: var(--vve-text-chat-text-color, #000);
      }
      #chat-container {
        height: 100%;
        width: 100%;
      }
      header {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      #close {
        cursor: pointer;
        padding-left: 10px;
      }
      #enter {
        margin-left: 8px;
      }

      #parent-enter {
        color: blue;
        transform: scale(2);
        margin-left: 8px;
      }

      .list-item {
        height: auto;
        padding-bottom: 15px;
      }
      #message-input-container {
        width: 100%;
        display: flex;
        align-items: center;
      }
      #messages-feed {
        overflow-y: auto;
      }
      .message-text {
        white-space: normal;
        word-break: break-all;
        padding: 0 10px;
      }
      #text-input {
        width: 90%;
      }
    `;
  }

  static get properties() {
    return {
      title: { type: String },
      room: { type: Object },
      parent: { type: Object },
      messages: { type: Array },
      message: { type: String },
      placeholder: { type: String },
      buttonText: { type: String },
    };
  }

  constructor() {
    super();
    this.title = 'Chat';
    this.room = {};
    this.parent = {};
    this.messages = [];
    this.message = '';
    this.messageFeed;
    this.feedAtBottom = false;
    this.placeholder = 'Type a message';
    this.buttonText = 'send';
  }

  isFeedAtBottom() {
    return (
      this.messageFeed.offsetHeight + this.messageFeed.scrollTop ===
      this.messageFeed.scrollHeight
    );
  }

  scrollFeedToBottom() {
    this.messageFeed.scrollTop = this.messageFeed.scrollHeight;
  }

  updated(changedProperties) {
    this.messageFeed = this.shadowRoot.querySelector('#messages-feed');
    if (changedProperties.get('room')) {
      this.room.on('signal:text_chat', signal => {
        let name = '';
        if (!signal.isSentByMe) {
          name = signal.from.name;
        } else {
          name = 'Me';
        }
        this.messages = [
          ...this.messages,
          {
            id: Date.now(),
            text: signal.data,
            name: name,
            isSentByMe: signal.isSentByMe,
          },
        ];
        this.feedAtBottom = this.isFeedAtBottom();
      });
    }

    if (changedProperties.get('parent')) {
      this.parent.on('signal:text_chat', signal => {
        let name = 'Parent: ';
        this.messages = [
          ...this.messages,
          {
            id: Date.now(),
            text: signal.data,
            name: name,
            isSentByMe: signal.isSentByMe,
          },
        ];
        this.feedAtBottom = this.isFeedAtBottom();
      });
    }
    if (this.feedAtBottom) {
      // const messageElems = this.shadowRoot.querySelectorAll('.message');
      const messageElems = this.shadowRoot.querySelectorAll('.list-item');
      messageElems[this.messages.length - 1].scrollIntoView();
    }
  }

  async __handleClickEvent() {
    if (this.message) {
      this.room.signal({
        data: this.message,
        type: 'text_chat',
      });
      this.message = '';
    }
  }

  async __handleParentClickEvent() {
    if (this.message) {
      this.parent.signal({
        data: this.message,
        type: 'text_chat',
      });
      this.message = '';
    }
  }

  async __handleInputEvent(e) {
    this.message = e.target.value;
  }

  __closeComponent(e) {
    const detail = { message: 'close chat window' };
    const event = new CustomEvent('close', {
      detail,
      bubbles: true,
      composed: true,
      cancelable: true,
    });
    this.dispatchEvent(event);
  }

  render() {
    return html`
      <section part="container" id="chat-container">
        <header part="header">
          <p part="title">${this.title}</p>
          <svg
            id="close"
            part="close-button"
            @click="${this.__closeComponent}"
            xmlns="http://www.w3.org/2000/svg"
            enable-background="new 0 0 24 24"
            height="24"
            viewBox="0 0 24 24"
            width="24"
          >
            ${unsafeSVG(close)}
          </svg>
        </header>
        <div id="messages-container" part="messages-container">
          <div id="messages-feed" part="messages-feed">
            ${this.messages.map(
              message => html`
                <div class="list-item">
                  <div>
                    <span class="username" part="username"
                      >${message.name.replace(/</g, '&lt;')}</span
                    >
                    <span part="time"
                      >${new Date(message.id).toLocaleTimeString('en-US')}</span
                    >
                  </div>
                  <div
                    class="message-text"
                    part="message-text"
                    slot="secondary"
                  >
                    ${message.text.replace(/</g, '&lt;')}
                  </div>
                </div>
              `
            )}
          </div>
        </div>
        <div id="message-input-container" part="message-input-container">
          <mwc-textfield
            id="text-input"
            outlined
            dense
            placeholder=${this.placeholder}
            .value=${this.message}
            @input="${this.__handleInputEvent}"
          >
          </mwc-textfield>

          <mwc-button
            id="enter"
            outlined
            @click="${this.__handleClickEvent}"
            label="send"
          ></mwc-button>
          <br />

          <svg
            id="parent-enter"
            part="enter-button"
            @click="${this.__handleParentClickEvent}"
            xmlns="http://www.w3.org/2000/svg"
            enable-background="new 0 0 24 24"
            height="24"
            viewBox="0 0 24 24"
            width="24"
          >
            ${unsafeSVG(play)}
          </svg>
        </div>
      </section>
    `;
  }
}
